.App{
    width: 100vw;
    height: 100vh;
    display: flex;
    
}

.leftSide{
    display: flex;
    width: 50%;
    flex-direction: column;
    margin-top: 15%;
    justify-content:center;
    align-items: center;
}

.picRohan{
    width: 65%;
    height:auto;
}

.rightSide{ 
    width: 50%;
    display: flex;
    justify-content:center;
    align-items: center;
}

html{
    background-color: white;
}

h1{
    margin: 0;
    font-family: "Mont-Heavy";
    text-align: center  ;
    font-size: 50px;
}

h2{
    font-family: "Mont-ExtraLight";
    text-align: center;
    color: black;
}

.buttonContainer a{
    margin: 50px;
    background-color: grey;
    color: black;
    text-decoration: none;
    padding: 10px 10px;
    border-radius: 8px;
    transition: 0.2s;

}

.buttonContainer a:hover{
    background-color: aqua;
    transform: scale(1.1);
}

.socialIcon{
    margin: 100px;
    width: 50px;
    height:auto;
    color:black;
    
}


a{
    color: black;
}
.Project h3{
    margin: 5%;
    font-family: "Mont-ExtraLight";
    text-align: left;
    margin-right: 30px;
    max-width: 50%;
    font-size: 25px;
}

.skillsPage{
    display: flex;
    flex-direction: column;
    max-width: 90%;
    
}

.Project{
    display: flex;
    margin-top: 10%;
    justify-content: space-between;
    align-items: center;
    background-color: whitesmoke;
    border-radius: 50px;
    padding: 25px;
}

.skillsPageContainer{
    width: 100vw;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Mont-ExtraLight";
}

@media screen and (max-width: 900px){
    .Project{
        
    }
}
    
